	module.exports = {"data":{"challengeNode":{"title":"Target the Parent of an Element Using jQuery","description":["每个 HTML 标签都默认<code>继承（inherits）</code>其<code>父标签（parent element）</code>的 CSS 属性。","例如，<code>h3</code>标签<code>jQuery Playground</code>的父标签是<code>&#60;div class=\"container-fluid\"&#62</code>，<code>&#60;div class=\"container-fluid\"&#62</code>的父标签是<code>body</code>。","jQuery 有一个<code>parent()</code>方法，可以访问被选取标签的父标签。","下面的代码展示了使用<code>parent()</code>方法把<code>left-well</code>标签的父标签背景色设置成<code>蓝色（blue）</code>的方式：","<code>$(\"#left-well\").parent().css(\"background-color\", \"blue\")</code>","请把<code>#target1</code>标签的父标签背景色设置成<code>红色（red）</code>。"],"challengeType":0,"fields":{"slug":"/front-end-libraries/jquery/target-the-parent-of-an-element-using-jquery","blockName":"jQuery","tests":[{"text":"<code>left-well</code>标签应该有红色的背景。","testString":"assert($(\"#left-well\").css(\"background-color\") === 'red' || $(\"#left-well\").css(\"background-color\") === 'rgb(255, 0, 0)' || $(\"#left-well\").css(\"background-color\").toLowerCase() === '#ff0000' || $(\"#left-well\").css(\"background-color\").toLowerCase() === '#f00', '<code>left-well</code>标签应该有红色的背景。');"},{"text":"应该用<code>&#46;parent&#40;&#41;</code>方法修改该标签。","testString":"assert(code.match(/\\.parent\\s*\\(\\s*\\)\\s*\\.css/g), '应该用<code>&#46;parent&#40;&#41;</code>方法修改该标签。');"},{"text":"应该在<code>&#35;target1</code>标签上调用<code>&#46;parent&#40;&#41;</code>方法。","testString":"assert(code.match(/\\$\\s*?\\(\\s*?(?:'|\")\\s*?#target1\\s*?(?:'|\")\\s*?\\)\\s*?\\.parent/gi), '应该在<code>&#35;target1</code>标签上调用<code>&#46;parent&#40;&#41;</code>方法。');"},{"text":"仅用 jQuery 给标签添加类。","testString":"assert(code.match(/<div class=\"well\" id=\"left-well\">/g), '仅用 jQuery 给标签添加类。');"}]},"required":[{"link":"https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/3.3.7/css/bootstrap.css","raw":null,"src":null}],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<script>\n  $(document).ready(function() {\n    $(\"#target1\").css(\"color\", \"red\");\n    $(\"#target1\").prop(\"disabled\", true);\n    $(\"#target4\").remove();\n    $(\"#target2\").appendTo(\"#right-well\");\n    $(\"#target5\").clone().appendTo(\"#left-well\");\n\n  });\n</script>\n\n<!-- 请修改本行以上的代码 -->\n\n<body>\n  <div class=\"container-fluid\">\n    <h3 class=\"text-primary text-center\">jQuery Playground</h3>\n    <div class=\"row\">\n      <div class=\"col-xs-6\">\n        <h4>#left-well</h4>\n        <div class=\"well\" id=\"left-well\">\n          <button class=\"btn btn-default target\" id=\"target1\">#target1</button>\n          <button class=\"btn btn-default target\" id=\"target2\">#target2</button>\n          <button class=\"btn btn-default target\" id=\"target3\">#target3</button>\n        </div>\n      </div>\n      <div class=\"col-xs-6\">\n        <h4>#right-well</h4>\n        <div class=\"well\" id=\"right-well\">\n          <button class=\"btn btn-default target\" id=\"target4\">#target4</button>\n          <button class=\"btn btn-default target\" id=\"target5\">#target5</button>\n          <button class=\"btn btn-default target\" id=\"target6\">#target6</button>\n        </div>\n      </div>\n    </div>\n  </div>\n</body>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[{"link":"https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/3.3.7/css/bootstrap.css","raw":null,"src":null}],"nextChallengePath":"/front-end-libraries/jquery/target-the-children-of-an-element-using-jquery","id":"bad87fee1348bd9aed308826"},"slug":"/front-end-libraries/jquery/target-the-parent-of-an-element-using-jquery"}}

